@use 'base/variables' as v;
@use 'base/mixins' as m;

.inicio {
    background-image: url(../../../build/img/header.webp);
}

.header {
    background-color: v.$grisOscuro;
    padding: 1rem 0 3rem 0;

    &.inicio {
        background-position: center center;
        background-size: cover;

        @include m.tablet {
            min-height: 70rem;
        }

        .contenido-header {
            @include m.tablet {
                min-height: 70rem;
            }
        }
    }

    .contenido-header {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        h1 {
            text-align: left;
            color: v.$blanco;
            font-weight: bold;
            max-width: 60rem;
        }
    }
}
.mobile-menu{
    max-width: 5rem;
    margin: 0 auto;
    @include m.tablet
    {
       display: none;
    }
}