@use 'base/variables' as v;
@use 'base/mixins' as m;

.iconos-nosotros {
    @include m.tablet {
        @include m.grid(3, 2rem)
    }
}

.icono {
    text-align: center;
    @include m.tablet{
    max-width: 40rem;
       
    }

    img {
        height: 10rem;
    }

    h3 {
        text-transform: uppercase;
    }
}