@use 'base/mixins' as m;
@use 'base/variables' as v;

.contenido-nosotros{
    @include m.tablet{
       @include m.grid(2,2rem)
    }
}
.texto-nosotros blockquote{
    font-weight: v.$black;
    font-size: 2.2rem;
    margin: 0;
    padding: 0 2rem;
}
.resumen-propiedad{
    background-color: v.$grisOscuro;
    padding: 3rem;
    p{
        color: v.$blanco;
    }
    .precio{
        color:v.$verde;
        font-size: 3rem;
        font-weight: bold;
    }
}