@use 'base/variables' as v;
@use 'base/mixins' as m;
.botonAmarillo{
    @include m.boton(v.$amarillo, block);
}
.boton-verde,
    input.boton-verde {
    @include m.boton(v.$verde);
    border: none;
    cursor: pointer;
}
.boton-verdeBlock{
    @include m.boton(v.$verde,inline-block);
    border: none;
    cursor: pointer;
}
.botonAmarillo2{
    @include m.boton(v.$amarillo, inline-block);
}
.botonRojo-block{
    @include m.boton(v.$rojo, block);
    margin: .5rem 0;
    width: 100%;
    border: none;
}