@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenedor-anuncios {
    margin: 0 2rem;

    @include m.tablet {
        @include m.grid(3)
    }

    margin: 2rem 0;
}

.anuncio {
    background-color: v.$gris;
    border: 1px solid v.$grisOscuro;
    margin-bottom: 2rem;
    overflow: hidden;
    display: flex;
    flex-direction: column;

    @include m.tablet {
        max-width: 35rem;
    }

    a {
        font-size: 2.2rem;
    }
}

.contenido-anuncio {

    display: flex;
    flex-direction: column;
    flex: 1;
    padding: 2rem;

    h3,
    p {
        margin: 0;
    }

    p {
        overflow-wrap: anywhere; // corta palabras larguísimas
        word-break: break-word; // fallback
        hyphens: auto;
    }

}

.iconos-caracteristicas{
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-items: center;   // centra cada columna
  align-items: center;
  margin: 2rem 0;
  padding: 0;

  li{
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.8rem;
  }

  img{
    width: 2.8rem;
    height: auto;
  }

  p{
    margin: 0;
    font-weight: bold;
    font-size: 2.4rem;
    line-height: 1;
  }
}


.botonAmarillo {
    @include m.boton(v.$amarillo, block);
      margin-top: auto;
}

.boton-verde {
    @include m.boton(v.$verde, block);

}

.precio {
    color: v.$verde;
    font-size: 3rem;
    font-weight: bold;
}