.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.botonAmarillo {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
}
.botonAmarillo:hover {
  background-color: rgb(174.9699570815, 105.4506437768, 7.0300429185);
  cursor: pointer;
}

.boton-verde,
input.boton-verde {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: flex;
}
.boton-verde:hover,
input.boton-verde:hover {
  background-color: rgb(80.4406779661, 126, 0);
  cursor: pointer;
}
.boton-verde,
input.boton-verde {
  border: none;
  cursor: pointer;
}

.boton-verdeBlock {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: inline-block;
}
.boton-verdeBlock:hover {
  background-color: rgb(80.4406779661, 126, 0);
  cursor: pointer;
}
.boton-verdeBlock {
  border: none;
  cursor: pointer;
}

.botonAmarillo2 {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: inline-block;
}
.botonAmarillo2:hover {
  background-color: rgb(174.9699570815, 105.4506437768, 7.0300429185);
  cursor: pointer;
}

.botonRojo-block {
  background-color: rgb(250, 20, 20);
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
}
.botonRojo-block:hover {
  background-color: rgb(214.4375, 4.5625, 4.5625);
  cursor: pointer;
}
.botonRojo-block {
  margin: 0.5rem 0;
  width: 100%;
  border: none;
}

.darkMode {
  background-color: #000000;
}
.darkMode p, .darkMode h1, .darkMode h2, .darkMode h3, .darkMode a {
  color: #FFFFFF;
}
.darkMode .anuncio {
  background-color: #333333;
  border: solid 1px #333333;
}
.darkMode .anuncio .precio {
  color: #71B100;
}
.darkMode .icono-caracteristicas li img {
  filter: invert(100%);
}
.darkMode form {
  color: #e1e1e1;
}
.darkMode form p, .darkMode form legend {
  color: #FFFFFF;
}

.alerta.error {
  background-color: rgb(250, 20, 20);
}
.alerta {
  text-align: center;
  padding: 1rem;
  color: #FFFFFF;
  text-transform: uppercase;
  margin: 1rem 0;
  font-weight: 700;
}
.alerta.ok {
  background-color: #71B100;
}

.inicio {
  background-image: url(../../../build/img/header.webp);
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}
.header .contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.header .contenido-header h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: bold;
  max-width: 60rem;
}

.mobile-menu {
  max-width: 5rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}

.footer {
  padding: 2rem 0;
  margin-bottom: 0;
  background-color: #333333;
}
.footer .contenedor-footer {
  padding-top: 5rem/2;
}

.copy {
  color: #FFFFFF;
  text-align: center;
}

.barra {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    padding-top: 3rem;
    align-items: flex-end;
  }
}

.navegacion {
  height: 0;
  visibility: hidden;
  opacity: 0;
}
@media (min-width: 768px) {
  .navegacion {
    opacity: 1;
    display: flex;
    gap: 1.5rem;
    align-items: center;
    height: auto;
    visibility: visible;
  }
}
@media (min-width: 768px) {
  .navegacion a {
    font-size: 1.8rem;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}

.mostrar {
  height: auto;
  visibility: visible;
  opacity: 1;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear;
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.derecha img {
  max-width: 5rem;
  filter: invert(100%);
}
.derecha img:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.icono {
  text-align: center;
}
@media (min-width: 768px) {
  .icono {
    max-width: 40rem;
  }
}
.icono img {
  height: 10rem;
}
.icono h3 {
  text-transform: uppercase;
}

.contenedor-anuncios {
  margin: 0 2rem;
}
@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.contenedor-anuncios {
  margin: 2rem 0;
}

.anuncio {
  background-color: #e1e1e1;
  border: 1px solid #333333;
  margin-bottom: 2rem;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .anuncio {
    max-width: 35rem;
  }
}
.anuncio a {
  font-size: 2.2rem;
}

.contenido-anuncio {
  display: flex;
  flex-direction: column;
  flex: 1;
  padding: 2rem;
}
.contenido-anuncio h3,
.contenido-anuncio p {
  margin: 0;
}
.contenido-anuncio p {
  overflow-wrap: anywhere;
  word-break: break-word;
  hyphens: auto;
}

.iconos-caracteristicas {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-items: center;
  align-items: center;
  margin: 2rem 0;
  padding: 0;
}
.iconos-caracteristicas li {
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.8rem;
}
.iconos-caracteristicas img {
  width: 2.8rem;
  height: auto;
}
.iconos-caracteristicas p {
  margin: 0;
  font-weight: bold;
  font-size: 2.4rem;
  line-height: 1;
}

.botonAmarillo {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
}
.botonAmarillo:hover {
  background-color: rgb(174.9699570815, 105.4506437768, 7.0300429185);
  cursor: pointer;
}
.botonAmarillo {
  margin-top: auto;
}

.boton-verde {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
}
.boton-verde:hover {
  background-color: rgb(80.4406779661, 126, 0);
  cursor: pointer;
}

.precio {
  color: #71B100;
  font-size: 3rem;
  font-weight: bold;
}

.imagen-contacto {
  background-image: url("../../../build/img/encuentra.webp");
  background-position: center center;
  background-size: cover;
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-contacto h2 {
  font-size: 4rem;
  font-weight: bold;
  color: #FFFFFF;
}
.imagen-contacto p {
  font-size: 2.8rem;
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .contenido-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.texto-nosotros blockquote {
  font-weight: 900;
  font-size: 2.2rem;
  margin: 0;
  padding: 0 2rem;
}

.resumen-propiedad {
  background-color: #333333;
  padding: 3rem;
}
.resumen-propiedad p {
  color: #FFFFFF;
}
.resumen-propiedad .precio {
  color: #71B100;
  font-size: 3rem;
  font-weight: bold;
}

.formulario textarea {
  height: 20rem;
}
.formulario p {
  font-size: 1.4rem;
  color: #333333;
  margin: 1rem 0;
}
.formulario legend {
  font-size: 2rem;
  color: #333333;
}
.formulario label {
  text-transform: uppercase;
  display: block;
  font-weight: 700;
}
.formulario input:not([type=submit]), .formulario textarea, .formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}

.forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.forma-contacto input[type=radio] {
  margin: 0;
  width: auto;
}

.resumen-propiedad {
  margin-top: 3rem;
  background-color: #333333;
}
.resumen-propiedad .precio {
  font-size: 3rem;
  font-weight: 700;
  color: #71B100;
}
.resumen-propiedad .iconos-caracteristicas {
  display: flex;
  gap: 2rem;
  justify-content: space-around;
  margin-bottom: 3rem;
  padding: 0;
}
.resumen-propiedad .iconos-caracteristicas li {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.resumen-propiedad .iconos-caracteristicas li img {
  width: 5rem;
}
.resumen-propiedad .iconos-caracteristicas li p {
  font-size: 2rem;
  font-weight: 700;
  margin: 0;
  color: #FFFFFF;
}
.resumen-propiedad p {
  font-size: 1.6rem;
  line-height: 1.8;
  margin-bottom: 2rem;
}
.resumen-propiedad p:last-of-type {
  margin-bottom: 0;
}

.propiedad-descripcion {
  color: #FFFFFF;
}

.tituloPropiedad {
  font-weight: 700;
  font-size: 5rem;
}

table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table.propiedades thead {
  background-color: #71B100;
}
table.propiedades thead th {
  color: #FFFFFF;
}
table.propiedades .imagen-tabla {
  width: 15rem;
}

.imagenChiquita {
  width: 40rem;
}

.w-100 {
  width: 100%;
}