@use 'base/variables' as v;
@use 'base/mixins' as m;

.barra {
    display: flex;
    flex-direction: column;

    @include m.tablet {
        flex-direction: row;
        justify-content: space-between;
        padding-top: 3rem;
        align-items: flex-end;
    }
}

.navegacion {
    height: 0;
    visibility: hidden;
    opacity: 0;

    @include m.tablet {
        opacity: 1;
        display: flex;
        gap: 1.5rem;
        align-items: center;
        height: auto;
        visibility: visible;
    }

    a {
        @include m.tablet {
            font-size: 1.8rem;
        }

        color: v.$blanco;
        text-decoration: none;
        font-size: 2.2rem;
        display: block;
        text-align: center;
    }
}

.mostrar {
    height: auto;
    visibility: visible;
    opacity: 1;
    transition: visibility .3s ease-in-out, opacity .5s linear;
}

.derecha {
    display: flex;
    flex-direction: column;
    align-items: center;

    img {
        max-width: 5rem;
        filter:invert(100%);
        &:hover{
            cursor: pointer;
        }
    }

    @include m.tablet {
        align-items: flex-end;
    }
}