@use 'base/variables' as v;
@use 'base/mixins' as m;
.formulario{
    textarea{
        height: 20rem;
    }
    p{
        font-size: 1.4rem;
        color:v.$grisOscuro;
        margin: 1rem 0;

    }
    legend{
        font-size: 2rem;
        color:v.$grisOscuro;

    }
    label{
        text-transform: uppercase;
        display: block;
        font-weight: v.$bold;
    }
    input:not([type="submit"]),textarea,select{
        padding: 1rem;
        display: block;
        width: 100%;
        background-color: v.$blanco;
        border: 1px solid v.$gris;
        border-radius: 1rem;
        margin-bottom: 2rem;
    }
}
.forma-contacto{
    max-width: 30rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    input[type="radio"]{
        margin: 0;
        width: auto;
    }
}