@use 'base/mixins' as m;
@use 'base/variables' as v;
.resumen-propiedad {
    margin-top: 3rem;
    background-color: v.$grisOscuro;

    .precio {
        font-size: 3rem;
        font-weight: v.$bold;
        color: v.$verde;
    }

    .iconos-caracteristicas {
        display: flex;
        gap: 2rem;
        justify-content: space-around;
        margin-bottom: 3rem;
        padding: 0;

        li {
            display: flex;
            align-items: center;
            gap: 1rem;

            img {
                width: 5rem;
            }

            p {
                font-size: 2rem;
                font-weight: v.$bold;
                margin: 0;
                color:v.$blanco
            }
        }
    }

    p {
        font-size: 1.6rem;
        line-height: 1.8;
        margin-bottom: 2rem;

        &:last-of-type {
            margin-bottom: 0;
        }
    }
}
.propiedad-descripcion{
    color:v.$blanco;
}
.tituloPropiedad{
    font-weight: v.$bold;
    font-size: 5rem;
}
table.propiedades{
    margin-top: 4rem;
    width: 100%;
    border-spacing: 0;
    thead{
        background-color: v.$verde;
        th{
            color:v.$blanco;
        }
    }
    .imagen-tabla{
        width: 15rem;
    }
}
.imagenChiquita{
    width: 40rem;
}
.w-100{
    width: 100%;
}