@use 'base/variables' as v;
@use 'base/mixins' as m;
.alerta{
    &.error{
        background-color: v.$rojo;
    }
    text-align: center;
    padding: 1rem;
    color: v.$blanco;
    text-transform: uppercase;
    margin: 1rem 0;
    font-weight: v.$bold;
    &.ok{
        background-color: v.$verde;
    }
}