@use 'base/variables' as v;
@use 'base/mixins' as m;

.imagen-contacto {
    background-image: url('../../../build/img/encuentra.webp');
    background-position: center center;
    background-size: cover;
    padding: 5rem 0;
    display: flex;
    flex-direction:column ;
    align-items: center;

    h2 {
        font-size: 4rem;
        font-weight: bold;
        color: v.$blanco;
    }

    p {
        font-size: 1.4rem*2;
        color: v.$blanco;
    }
}