@use 'base/mixins' as m;
@use 'base/variables' as v;
.darkMode{
    background-color: v.$negro;
    p,h1,h2,h3,a{
        color:v.$blanco;
    }
    .anuncio{
        background-color: v.$grisOscuro;
        border: solid 1px v.$grisOscuro;
        .precio{
            color:v.$verde;
        }
    }
    .icono-caracteristicas{
        li{
            img{
                filter: invert(100%);
            }
        }
    }
    form{
        color:v.$gris;
        p,legend{
            color:v.$blanco
        }
    }
}